import server from "./server";

// aboutUs page data

export const getAboutUsData = async () => {
  const response = await server.get("user/about-us");
  return response.data.data;
};

export const getCommonQuestion = async () => {
  const response = await server.get("user/common-questions");
  return response.data.data;
};

export const getTermsAndConditions = async () => {
  const response = await server.get("user/conditions");
  return response.data.data;
};
