import {
  getAboutUsData,
  getCommonQuestion,
  getTermsAndConditions,
} from "api/about-us-page";
import React, { useEffect, useState } from "react";
import { Container, Tabs, Tab } from "react-bootstrap";
import { useIntl } from "react-intl";
import { useLocation } from "react-router-dom";

// needed styles
import styles from "./index.module.scss";

const AboutUsPage = () => {
  const [activeKey, setActiveKey] = useState("aboutUs");
  const intel = useIntl();

  let location = useLocation();
  useEffect(() => {
    const hash = location.hash.replace("#", "");
    setActiveKey(hash);
  }, [location]);

  const [data, setData] = useState({
    aboutUs: "",
    commonQuestion: "",
    termsAndConditions: "",
  });

  useEffect(() => {
    const fetchData = async () => {
      const [
        aboutUsResponse,
        commonQuestionResponse,
        termsAndConditionsResponse,
      ] = await Promise.all([
        getAboutUsData(),
        getCommonQuestion(),
        getTermsAndConditions(),
      ]);

      setData({
        aboutUs: aboutUsResponse[0]?.body_ar,
        commonQuestion: commonQuestionResponse[0]?.body_ar,
        termsAndConditions: termsAndConditionsResponse[0]?.body_ar,
      });
    };
    fetchData();

    try {
    } catch (error) {
      console.log(error);
    }
  }, []);

  const tabItems = [
    {
      id: "aboutUs",
      eventKey: `  ${intel.formatMessage({ id: "about-us" })}`,
      children: `${data?.aboutUs}`,
    },
    {
      id: "commonQuestions",
      eventKey: `  ${intel.formatMessage({ id: "common-questions" })}`,
      children: `${data?.commonQuestion}`,
    },
    {
      id: "termsAndConditions",
      eventKey: `  ${intel.formatMessage({ id: "terms-conditions" })}`,
      children: `${data?.termsAndConditions}`,
    },
  ];

  return (
    <div>
      <Container className={styles.body}>
        <Tabs
          activeKey={activeKey}
          // defaultActiveKey={activeKey}
          id="uncontrolled-tab-example"
          className={`mb-3  dynamic-pages-tabs `}
          onSelect={(k) => setActiveKey(k)}
        >
          {tabItems?.map((tab) => (
            <Tab
              key={tab?.id}
              eventKey={tab?.id}
              title={tab?.eventKey}
              id={tab?.eventKey}
            >
              <div dangerouslySetInnerHTML={{ __html: tab?.children }} />
            </Tab>
          ))}
        </Tabs>
      </Container>
    </div>
  );
};

export default AboutUsPage;
